/* src/components/Notice.css */

.notice-container {
    width: 50%;
    height: 50px;
    padding: 10px;
    background-color: #222;
    border-radius: 8px;
    margin-top: 20px;
    overflow: hidden;
    position: relative;
    display: flex;
    align-items: center;
    white-space: nowrap;
  }
  
  .notice-text {
    color: #ffd700;
    font-size: 18px;
    font-weight: bold;
    position: absolute;
    left: 100%;
    animation: slide 40s linear infinite;
    padding-left: 20px;
  }
  
  @keyframes slide {
    0% {
      left: 100%;
    }
    100% {
      left: -100%;
    }
  }
  
  /* Ajuste para pantallas m치s peque침as (m칩viles) */
  @media (max-width: 768px) {
    .notice-text {
      font-size: 16px;
    }
  
    .notice-container {
      padding: 8px;
      height: 45px;
    }
  }
  