
/* 游늷 TABLA */
.table-container {
    width: 50%;
    max-width: 100%;
    text-align: center;
    background-color: #222;
    border-radius: 8px;
    padding: 10px;
    overflow-x: auto;
    color: white;   
}

/* 游늷 TABLA - ESTILOS */
.guild-table {
    width: 100%;
    border-collapse: collapse;
    border: 2px solid #555;
    background-color: #222;
}

.guild-table th,
.guild-table td {
    border: 1px solid #444;
    padding: 10px;
}

.guild-table tr:hover {
    background-color: #444;
}

.dead {
    background-color: rgba(128, 128, 128, 0.3); 
    opacity: 0.6; 
    color: #aaa;
}

.dead:hover {
    background-color: rgba(128, 128, 128, 0.4); 
}
  
.status-cell {
    font-weight: bold;
}  

/* 游늷 IM츼GENES */
.role-image {
    width: 40px;
    height: 40px;
    border-radius: 50%;
}

/* 游늷 TEXTO TWITCH */
.twitch-text {
    color: #9146ff;
    font-weight: bold;
}
