/* 游꿛 ESTILOS GENERALES */
.page-container {
    min-height: 100vh;
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    
    background-color: #121212;
    background-image: url('../assets/images/estandarte.png'), url('../assets/images/estandarte.png');
    background-position: left top, right top;
    background-repeat: no-repeat;
    background-size: 15% auto;
    background-attachment: fixed;
}

/* 游늷 IMAGEN DEL TITULO */
.page-image {
    width: 100%;
    max-width: 800px;
    height: auto;
    margin-bottom: 10px;
}

/* 游늷 CONTENEDORES PRINCIPALES */
.characters-information {
    display: flex;
    justify-content: space-between;
    width: 100%;
    max-width: 1200px;
    margin-top: 20px;
    gap: 20px;
    box-sizing: border-box;
}

/* 游님 RESPONSIVE DESIGN */
@media (max-width: 1024px) {
    .page-image {
        max-width: 90%;
    }

    .characters-information {
        flex-direction: column;
        align-items: center;
    }

    .left-sidebar, .right-sidebar, .table-container {
        width: 100%;
        max-width: 90%;
        margin-bottom: 20px;
    }
}

@media (max-width: 768px) {
    .page-image {
        max-width: 80%;
    }

    .guild-title {
        font-size: 36px;
    }

    .guild-warning {
        font-size: 20px;
    }

    .table-container {
        overflow-x: auto;
    }

    .guild-table {
        width: 100%;
        min-width: 600px;
    }

    .role-image {
        width: 35px;
        height: 35px;
    }
}

@media (max-width: 480px) {
    .page-image {
        max-width: 70%;
    }
    
    .guild-title {
        font-size: 28px;
    }

    .guild-warning {
        font-size: 18px;
    }

    .characters-information {
        flex-direction: column;
        align-items: center;
    }

    .left-sidebar, .right-sidebar {
        width: 100%;
        text-align: center;
    }

    .guild-table {
        width: 100%;
        min-width: 500px;
    }

    .role-image {
        width: 30px;
        height: 30px;
    }
}
