/* 游늷 BARRAS LATERALES */
.left-sidebar {
    width: 25%;
    max-width: 100%;
    text-align: left;
    padding: 10px;
    background-color: #222;
    border-radius: 8px;
    flex-grow: 0;
    color: white;
}


/* 游늷 LISTAS */
.role-list, .class-list {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.role-list div, .class-list div {
    margin-bottom: 10px;
}
