@font-face {
    font-family: 'Folkard'; /* Nombre que usar치s en CSS */
    src: url('../../fonts/folkard_.ttf') format('truetype'); /* Ruta de la fuente */
    font-weight: normal;
    font-style: normal;
  }

.title-lowbar {
    width: 100%;
    background-color: #222;
    border-radius: 8px;
    padding: 15px;
    margin-top: 20px;
    box-shadow: 0px 0px 10px rgba(255, 215, 0, 0.2);
    box-sizing: border-box;
    text-align: center;
}

.title-lowbar-heading {
    font-family: 'Folkard', sans-serif;
    font-size: 28px;
    font-weight: bold;
    color: #ffd700;
    margin-bottom: 15px;
}

.title-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 12px;
}

.title-item {
    background-color: #333;
    padding: 12px;
    border-radius: 5px;
    font-size: 16px;
    font-weight: bold;
    text-align: center;
    transition: background 0.3s ease;
}

.title-item:hover {
    background-color: #444;
}

.title-name {
    font-family: 'Folkard', sans-serif;
    display: block;
    color: #ffd700;
    font-size: 18px;
}

.title-owner {
    display: block;
    color: #9146ff;
    font-weight: bold;
    font-size: 14px;
}

.title-desc {
    display: block;
    color: #aaa;
    font-size: 13px;
}

.no-titles {
    font-size: 16px;
    color: #bbb;
    font-style: italic;
}